.Paginate{
    position: absolute;
    width: 100%;
    height: 8vh;
    background: rgb(20, 20, 20);
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
    
}
.setpage{
    border: none;
    padding: 1vh 3vh;
    background: blue;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 3vh;
    transition: all 1s ease-in;
}
.setpage:hover{
    background: rgb(78, 70, 70);
}
.setpage:active{
    transform: translateX(0px);
}
.setpage:disabled{
    cursor:not-allowed;
    background: rgb(105, 104, 104);
    
}
.setpage:disabled:hover{
    background: rgb(105, 104, 104);
}
.setpage:disabled:active{
    background: rgb(105, 104, 104);
}
